<template>
  <div class="my">
    <div class="my-box img-tm" :style="{backgroundImage: 'url(' + userInfo.userBg + ')'}">
      <div class="change-bg clearfix">
        <div class="fr cbg" v-if="isLogin">
          <div>更换背景</div>
          <van-uploader class="upload-box" :max-size="maxSize" :after-read="uploadFile" />
        </div>
      </div>
      <div class="user-info">
        <div v-if="isLogin">
          <div class="clearfix">
            <div class="my-img fl">
              <img class="auto-imgs" :src="userInfo.userImg" alt />
            </div>
            <div class="nickname fl">{{userInfo.nickName}}</div>
          </div>
          <div class="text">简介：{{userInfo.desc == '' ? '这小可爱什么都没留下！' : userInfo.desc}}</div>
        </div>
        <div class="login-text" v-else @click="goPage('Login')">登录</div>
      </div>
    </div>

    <div>
      <div class="clearfix">
        <div class="fl my-box-list">
          <div class="my-money">0.00</div>
          <div class="my-tiltle">用户</div>
        </div>
        <div class="fl my-box-list">
          <div class="my-money">0</div>
          <div class="my-tiltle">粉丝</div>
        </div>
        <div class="my-recharge fr" @click="onToast">充值</div>
      </div>
      <div  class="my-border">
      <van-cell
        :title="item.title"
        :icon="item.icon"
        is-link
        v-for="(item,index) in litsData"
        :key="index"
        @click="goPage(item.routeName)"
      />
      <van-cell title="分享应用" icon="share" is-link @click="showShare = true" />
      </div>
    </div>
    <van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" @select="onSelect" />
  </div>
</template>

<script>
import { Toast } from "vant";
import "../../assets/css/my.less";
export default {
  data() {
    return {
      //上传文件1MB
      maxSize: 1 * 1024 * 1024,
      litsData: [
        {
          title: "账号管理",
          routeName: "Account",
          icon: "diamond-o",
        },
        {
          title: "阅读记录",
          routeName: "Bookshelf",
          icon: "records",
        },
        {
          title: "安全中心",
          routeName: "Secure",
          icon: "bulb-o",
        },
      ],

      //用户信息
      userInfo: {},

      isLogin: false,

      showShare: false,
      options: [
        { name: "微信", icon: "wechat" },
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        { name: "分享海报", icon: "poster" },
        { name: "二维码", icon: "qrcode" },
      ],
    };
  },

  created() {
    this.getUserInfo();

    if (localStorage.getItem("__Tk")) {
      this.isLogin = true;
    }
  },

  methods: {
    //跳转页面
    goPage(name) {
      if (name === "Login") {
        this.$router.push({ name });
      } else {
        if (!this.isLogin) {
          this.$toast("请先登录");
        } else {
          this.$router.push({ name });
        }
      }
    },

    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //获取上传文件的base64
    uploadFile(file) {
      //获取图片base64
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //点击分享
    onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },
    onToast() {
      this.$toast("该软件暂时不能提供充值");
    },
  },
};
</script>

